 <div>
        <h3 class="py-6 text-3xl">Syons Calamitye or Englands Miserye Hieroglyphically Delineated</h3>
        <h4 class="py-2 text-xl">No.6 January 2007 </h4>
    </div>
    <div class='relative text-left overflow-y-auto'>
      <p class="py-2"> <strong> British Library Board. All Rights Reserved.</strong> </p>
      <p class="pt-2 pb-4"> <strong>British Museum 1868,0808.3356. Dimensions of original: 482 mm x 369 mm</strong></p> 
        <span class="float-left pr-6 pb-2"><img :src="thumbnail" width="200"/> <button type="button" @click="getlargerImage()" class="underline font-bold" >View larger image</button></span> 

      <p class="pb-2">A number of single-sheet prints attempt to summarise the state of the nation in the Civil War period in symbolic, rather than narrowly literal, ways. One such is <span class="italic"></span>Syons Calamitye or Englands Miserye Hieroglyphically Delineated (1643). This is one of the most interesting prints of the era, anonymously engraved, with text presumed to be by the otherwise unknown A. Jackman (who signs the dedication to the Earl of Northumberland<sup>i</sup>), and sold by the otherwise unknown Nathaniel Gilby. This fascinating symbolic depiction of the state of the nation survives in just two impressions, but deserves to be much better known, and thus merits a detailed description.  </p>

      <p class="pb-2">
        The Eye of Providence gazes down on King Charles and Law (a long-haired woman), kneeling in prayer, their upraised hands joined by a chain which passes through the crown and sceptre. In the centre of the plate is a rectangular enclosure in which two armies fight, and England (accompanied by Conscience and Justice) is trammelled within a <span class="italic">strong Compacted Nett</span>, held tight by evil councillors, and from which a bare-breasted Astrea (the spirit of Elizabeth I?) with sword upraised endeavours to free them. A philosopher advises King Charles to submit himself to Reason and to the <span class="italic">evill Crewe / Of wicked Counsellors declare theire dew</span>. With the gate to the enclosure defended by War, however, two bands led by Peace (with an olive branch) and Truth (holding a solar disc) are excluded, as are the commoners praying for the King, Church, Law and State. On either side of this central axis are three sections. The middle sections on either side represent the radically differing states of Ireland and Scotland, the former lamenting the war, murder, executions, and burnings by which she is surrounded, the latter curiously depicted as benefiting from the general confusion obtaining in England in two metaphorical vignettes: in one a Scotswoman picks up coins from the ground, saying, <span class="italic">Oh yow shall have dainty twopences</span>, while another sifts corn and says, <span class="italic">The wheat for me the chaffe for thee</span>.
      </p>

      <p class="pb-2">
        In the top righthand section we find Judgement and Reason chained to a post, while Passion in armour and a snake-haired Fury run amok, the former with sword and dagger in hand yelling, <span class="italic">Kill</span>, the latter carrying a flaming torch and shrieking, <span class="italic">Fire</span>. In another vignette a pair of asses pull Misery in a carriage of state, as a figure (identified as Famine in the text) runs past, and a skeleton (identified as Fate) with hourglass and dart in hand runs behind the carriage. Another pair of asses has collapsed under the weight of their burdens (labelled <span class="italic">Honores</span>) while a man representing the Commons attempts to hold them up, and another man holding a document says, <span class="italic">No Monopolies I beseech yow</span>, alluding to what had long been a bone of contention between Crown and Parliament.
      </p> 

      <p class="pb-2">
      The bottom righthand section, entitled <span class="italic">Phantasia Triumphans</span>, contains some most interesting miniature scenes evidently aimed at the sectaries of the day. A couple wearing crowns, but otherwise normally dressed, exclaim, <span class="italic">I shall be Kinge of Jerusalem</span> and <span class="italic">and I Queene</span>. Another man, labelled a <span class="italic">Vomere ad Rostra</span> [from the plough to the pulpit], with his index finger pointing heavenwards, says, <span class="italic">I am the Prophett sent</span>, a third in Puritan dress declares, <span class="italic">Ther's nothing to oppose us</span>, a fourth in Cavalier garb smoking a pipe, <span class="italic">All's our owne</span>, a fifth, <span class="italic">I Speake all extempore nonsence naturally</span>, while a group of four boys announces, <span class="italic">we are wiser then our Teachers</span> [Psalm 119.99]. A 'holier-than-thou' woman, perhaps intended as a Quaker, says to the smoking Cavalier, <span class="italic">Stand further of &c</span>, with a reference to Isaiah 65.5: <span class="italic"></span>Stand by thyself, come not near me; for I am holier than thou. These are a smoke in my nose... A final odd couple are a man (perhaps a divine) dressed in black holding a book who says, <span class="italic">Tu Sans Ratione Philautos</span> [Self-love, thou art without reason], to a plainly-dressed man holding an andiron on whose shoulder he places his hand. This curious attribute also appears in the hand of the man excreted by the Devil in the woodcut illustrating the title-page to <span class="italic">A Reply As True As Steele</span> (1641), John Taylor's attack on the 'mechanick' preacher Henry Walker, an ironmonger by trade. The present scene on the print is labelled <span class="italic">Hinc ill lachrim</span>, a quotation from Terence, literally meaning 'hence those tears', and used to refer to the real, underlying cause of a person's malaise. But the print designer has also added a reference to the third and fourth chapters of Paul's Second Epistle to Timothy. The second verse of the former chapter refers to the circumstances which will obtain <span class="italic">in the last days</span>, specifically, that <span class="italic">men shall be lovers of their own selves</span>, while in verse 14 of the latter chapter Paul remembers <span class="italic">Alexander the coppersmith</span> [who] <span class="italic">did me much evil</span>perhaps another oblique reference to Walker's profession.
      </p>

      <p class="pb-2">In the top lefthand section of the design, the Church (labelled <span class="italic">Ecclesia Languens</span>) is flanked by sincere but ineffectual Protestant divines who are opposed by Catholic and Laudian bishops labelled <span class="italic">Susorrores</span> (literally, 'whisperers', but rendered 'politicians' in the accompanying verse text) and <span class="italic">Adulatores</span> [flatterers].<sup>ii</sup> Four young students cast away their books (captioned <span class="italic">Victa jacet pietas</span> from Ovid's <span class="italic">Metamorphoses</span>'piety, defeated, lies dead'), and point admiringly at three armed Cavaliers. A final scene of two men labelled <span class="italic">Inconcionatores</span> [sic], referred to in the text as <span class="italic">Dunces</span>, appears to show a beadle (?) whipping a barefoot friar (his rosary hanging from his girdle) out of town; the latter exclaims <span class="italic">V mihi</span> [woe is me], but the pair are also captioned, <span class="italic">Nihil refert ad nos</span> [Nothing matters to us].</p>

      <p class="pb-2">The lower lefthand section, entitled <span class="italic">Lex Subjecta</span>, comprises a number of scenes to do with the exercise of the Law in the years immediately preceding publication of the print. Curiously, F.G. Stephens in his Victorian description of it, contented himself with describing the main scene as 'judges with ships and laurel wreaths upon their heads sitting neglected'judges with ships on their heads?<sup>iii</sup> Either he did not feel this required explanation orif I dare suggest it of this scholar, for whom I have the greatest admirationhe simply did not recognise the significance! He neglected to point out that the two of the six judges wearing laurel wreaths are trampling on ships, while the other four wearing the ship 'hats' are trampling on their laurels, nor did he refer to the two men beside the bench, one of whom similarly wears a ship on his head and drags behind him a large purse bearing the royal arms. He says, <span class="italic">Heu quanta de spe decidam</span> [Alas! from what a height of hope I will fall], to which his companion responds reassuringly, <span class="italic">Ne timeas</span> [Have no fear], but the purse is labelled <span class="italic">Hinc petitio et Perditio hujus</span> [From this place comes the writ and his ruin]. This must surely allude to the notorious Ship Money tax levied by Charles from 1635 onwards until abolished by statute in 1641. In 1637 John Hampden, the Buckinghamshire MP, brought a test case to court by refusing to pay the levy (the ostensible purpose of which was to finance the refitting of the navy); the judges were divided on the legality of the measure, though eventually a majority found in favour of the King on this occasion. Designed after the 1641 abolition, the minority of the bench who trample the ships, labelled <span class="italic">Memoria justi vivet in ternum</span> [The memory of the just will live eternally] are clearly 'the good guys', the others being labelled <span class="italic">Obliti sumus Parliamenti</span> [We were oblivious of parliament]. The accompanying verse text (perhaps tactfully) glosses over this particular topical and historic case, preferring to refer in much more general terms to the fact that <span class="italic">The Lawes... have bene abused, and the Lawe / Pul'd downe Injustice Raisd....</span> Another scene shows a panel of four unjust judges jeering at the plaintiff who kneels before them, saying</p>

      <p class="pb-2 italic">
      Our Canon<sup>iv</sup> <br>
      So shall he know<br>
      Choake him with an &c<br>
      The knav's wittie
      </p>

      <p class="pb-2">Two further scenes feature men wearing fools' caps: one, saying <span class="italic">Te flectere faciam</span> [I will make you bend], enacts the proverbial folly of trying to bend a stone pillar, which is itself labelled <span class="italic">cunctis eadem </span>(from the Vulgate text of Ecclesiastes 9.3; in the KJV: 'This is an evil among all things that are done under the sun, that there is one event unto all', i.e. death), while the other fool kicks and whips a dishevelled woman representing Conscience, who exclaims, <span class="italic">Est ne Deus</span> [Is there no God].<sup>v</sup> There is also a well with two men in buckets, one rising as the other descends; the man in the ascendancy says, <span class="italic">Hodie mihi cras tibi</span> [Me today, you tomorrow]a tag more usually found as a <span class="italic">memento mori</span>while the other says, <span class="italic"></span>Tempora mutantur [Times change]. Lastly, in the four corners of the engraving, four foreign rivals are depicted, gloating over the opportunities this chaos in England affords their respective countries. Their four labels form a quatrain, and they are also assigned mottoes as follows:</p>

      <p class="pb-2 italic">
      The Lord Dane cries if these hold I'le be cheefe<br>
      Redditum expecto [I expect to be restored]<br>
      The Hungrie Spaniard wisheth its good Befe<sup>vi</sup> <br>
      Valde Esurio [I desire it very much]<br>
      The smooth Tongu'd Frenchman sayes Oh! that's for me<br>
      Ista mihi [That's for me]<br>
      The Dutch man bids, hands of; that shall not be.<br>
      Non aude sed time [Do not dare but fear]
      </p>

      <p class="pb-2">At the bottom are three columns of verses keyed by letter to the various scenes in the print, and providing a commentary on them. </p>

      <p class="pb-2">
