 <div>
        <h3 class="py-6 text-3xl">This Ages Rarity: or The Emblem of a Good Servant Explaind</h3>
        <h4 class="py-2 text-xl">No. 8 March 2007 </h4>
    </div>
    <div class='relative text-left overflow-y-auto'>
      <p class="py-2"> <strong> British Library Board. All Rights Reserved.</strong> </p>
      <p class="pt-2 pb-4"> <strong>British Museum 1849,0315.85 [Sat 1120]. Dimensions of original: 474 mm x 312 mm (sheet), 276 mm x 177 mm (plate).</strong></p> 
        <span class="float-left pr-6 pb-2"><img :src="thumbnail" width="200"/> <button type="button" @click="getlargerImage()" class="underline font-bold" >View larger image</button></span> 

        <p class="pb-2">The <span class="italic">Stationers Registers</span> record the issue of a licence to Henry Bamford on 4th March 1577 for <span class="italic">The pourtraiture of A trusty servaunt</span>, sold to him by William Hoskins (exactly a year later it was assigned to Richard Jones). Though sadly not known to be extant, we can be certain that the image in question was neither of some particularly famous retainer, nor an idealised 'real' human servant, but the earliest-known English representative of the composite pictorial figure of the <span class="italic">Ideal Servant</span> who is given various animal characteristics appropriate to his duties.  </p>

        <p class="pb-2">
        John Hoskins' verses accompanying the Winchester College mural (see below) declare that the pig's snout signifies that the Ideal Servant will not be fussy about what he eats, the ass's ears, that he will bear his master's rages with patience, and the stag's feet, that he will be swift to carry out his errands (The padlocked lips, which indicate that he knows how to keep his master's secrets, while not of animal origin, are also a significant attribute, of course, but there is not space to explore this motif fully here). Only a very few years after the date of this entry in the <span class="italic">Registers</span>, in the early 1580s, and while still a school-boy, the future sergeant-at-law, John Hoskins, caused just such a figure to be painted on the wall outside the kitchens of Winchester College and subscribed descriptive verses of his own devising. The earliest copy of this famous and still extant image appears in a manuscript dated 1647 entitled <span class="italic">Schola Wichamica</span> in the Fellows' Library of Winchester College, drawn by Robert Mathew and entitled EFFIGIES SERVI COLLEGIATI (although, by this date, we know it had already been repainted three times).<sup>i</sup>  The young John Hoskins is known to have had an uncle named William of the same period as the London publisher (above), so that it is at least theoretically possible that it was he who provided his nephew with the original design.
        </p>

        <p class="pb-2">
        As so often, the device is not an original English invention.<sup>ii</sup> The great early print-collector, Ferdinand Columbus had already obtained two prints of this allegorical figure for his collection before his death in 1539, and Gilbert Cousin described this exemplary composite  the French had painted in their houses in his [<span class="italic">Oiketes sive</span>] <span class="italic">De Officio Famulorum</span> (Basel, 1535) - the details of which would have been known to the English via Thomas Chaloner's 1543 translation. A woodcut representation appeared in Dinckel's German translation of Cousin's book published in Erfurt in 1583, and on the cover of an edition issued in the same city a decade later where the woodcut image is labelled <span class="italic">Biltnis eines frommen Knechts</span> [portrait of a pious servant]. A French image contemporary with the <span class="italic"></span>Stationers Register entry does survive as a woodcut sheet, entitled <span class="italic">Le Bon Serviteur</span>, in the Bibliothque Nationale in Paris,<sup>iii</sup> and it is highly likely that the Serving Man listed in Thomas Jenner's advertisement of 1662 represented the same personage. 
        </p>

        <p class="pb-2">
        As things stand, however, an engraving dated 1682 and entitled <span class="italic">This Ages Rarity: or The Emblem of a Good Servant Explain'd</span>, is the earliest English example to survive.<sup>iv</sup>  The verse beneath explains the curious image:
        </p> 

        <p class="pb-2 italic">
        Hinds Feet he hath, to shew how nimble he<br>
        In the Dispatch of Business ought to be:<br>
        His Asses Ears and Muzzel'd Mouth do show<br>
        He should be swift to Hear, but to Speak slow.<br>
        His Meagre Look, and his Thin Panch,<sup>v</sup> declare,<br>
        That he should be content with slender Fare.
        </p>

        <p class="pb-2">But the motif is in fact, to be found earlier - though somewhat disguised - on two closely-related sheets, issued in 1641, one etched, one woodcut: Hollar's symbolic <span class="italic">Picture of a Pattenty</span> [i.e. Patentee], and a broadside with verses by John Taylor the Water Poet, entitled, <span class="italic">The Complaint of M. Tenter-hooke the Projector, and Sir Thomas Dodger the Patentee</span>, the woodcut to which - specifically, the portrait of Mr. Tenterhooke - is clearly based on Hollar's engraving. M. Dorothy George noted that in 1641 Monopolies officially ceased, and it is this historical fact that enables us to infer the date of both prints. She continues:.</p>

        <p class="pb-2">'It is a striking example of the application to politics of the folk-print in which persons are constructed of objects connected with their calling. A man with a wolf's head has fish-hook fingers which pull strings attached to money bags; his legs are screws. The rest of the body is made up of things that had been the subjects of monopoly: wine, playing-cards, coals, soap, pins, &c.<sup>vi</sup></p>

        <p class="pb-2">
        Another English print example of this sort of Arcimboldesque composite portrait is <router-link :to="`/printsInFocusView/12`" class="text-blue-600 dark:text-blue-500 hover:underline">A Jesuit Displaid</router-link> (c.1682).
        </p>

        <p class="pb-2">The <span class="italic">Picture of a Pattenty</span> is not signed by Hollar, but has long been quite reasonably attributed to him on purely stylistic grounds, and there is no reason not to think it his. That he was the inventor, however, can now be shown not to be the case, for what is clearly the same figure had already appeared some fifty years earlier in a series of eight engravings entitled <span class="italic">Litis Abusus</span> [the Abuse of Litigation], signed in the second state, <span class="italic">H[endrik]G[oltzius] fecit</span>, and dated 1597.<sup>vii</sup> </p>

        <p class="pb-2">
        In this series the figure in question is labelled <span class="italic">Lis</span> (in the Dutch caption rendered as Proces), i.e. Litigation, and he appears most clearly in the third print of the series, given the editorial title, <span class="italic">Avarice</span> is the root of all evil [Timothy 6.10, <span class="italic">Radix enim omnium malorum est cupiditas</span>, is quoted on the sheet]. It depicts a blindfold Cupid followed by <span class="italic">Lis</span> whose 'hands' are held by <span class="italic">Opinio</span> and <span class="italic">Testamenta</span> (with skull-head and a forged will for hat), with <span class="italic">Meum</span> and <span class="italic">Tuum</span> bringing up the rear (A popular phrase to express the rights of property, as OED succinctly defines the phrase <span class="italic">meum and tuum</span>). <span class="italic">Lis</span> wears doublet and hose and has a wolf's head; in place of legs and feet he stands on a pair of screws, and his hands are replaced by triple hooks. Where his chest should be is an open lion's maw.<sup>viii</sup> The symbolism of <span class="italic">Lis</span>'s make-up is nowhere explained, but in the fifth print of the series we see him seated on a throne devouring the homestead of a man labelled <span class="italic">Litigator</span> (which he is feeding the monster), while from the lion's maw in his chest, <span class="italic"></span>Lis vomits miniature versions of himself labelled <span class="italic"></span>Lis lites generans [Litigation begets lawsuits]. To the right of <span class="italic">Lis</span>'s throne are two female figures with appropriate attributes labelled <span class="italic">Negligentia domus</span> [Neglect of house] and <span class="italic">Negligentia temporis</span> [Neglect of time], and a third female, <span class="italic">Negligentia animae</span> [Neglect of soul], who hangs the soul of the litigant over a rack. A miniature version of <span class="italic">Lis</span> appears in the final print of the series flying in on demonic wings to receive the soul of the dying and now destitute litigant.
        </p>

        <p class="pb-2">Hollar clearly adapted the figure of <span class="italic">Lis</span> as portrayed in this late sixteenth-century series for his <span class="italic">Picture of a Pattenty</span> of 1641. Curiously, Hollar's caption both alludes to the figure's <span class="italic">Hogshead</span> and terms it a <span class="italic">Wolfe like devourer of the Common wealth</span> - curious, because the monster has a wolf's rather than a hog's head. The <span class="italic">Hogshead</span> perhaps puns on the liquid measure of that name, a large cask for liquor, but though Taylor's text alludes to the monopoly on wine, the verses to the Hollar engraving do not: here, by implication, it is left to a flagon labelled <span class="italic">Wine P</span>.[atent] on the figure's sleeve. It is even posible that hogshead is a mistake for oxhead (see below). Playing upon the familiar proverb All is fish that comes to net,<sup>ix</sup> the caption's <span class="italic">All[']s Fish comes to his hooke</span> refers the reader back to the hooks in place of fingers on the creature's hands. With a deceptively modern ring to it,<sup>x</sup> the final exultant couplet reads: </p>

        <p class="pb-2 italic">
        Strong scrues support him that hath scru'd us all,<br>
        And now we live, to see this strong man fall.
        </p>

        <p class="pb-2">
          There is nothing porcine about either Hollar's figure or that illustrating Taylor's broadside, so it is again surprising to read the latter's verses describing the woodcut <span class="italic">M. Tenter-hooke</span>:
        </p>

        <p class="pb-2 italic">
        For you (as in my Picture plain appeares)<br>
        I put a Swines face on, an Asses eares,<br>
        The one to listen unto all I heard<br>
        Wherein your worships profit was prefer'd.<sup>xi</sup><br>  
        The other to tast all things, good or bad<br>
        (As Hogs will doe) where profit may be had.
        </p>

        <p class="pb-2">Taylor must here be thinking of the motif of the <span class="italic">Trusty Servant</span>, as in the Winchester College mural (above) and the lost print entered in the <span class="italic">Stationers Register</span> in 1577. Indeed, the Water Poet had made use of the same commonplace in one of his poems some years earlier,<sup>xii</sup> and the present verses make it clear that he sees <span class="italic">M. Tenter-hooke the Projector</span> as the servant of <span class="italic">Sir Thomas Dodger the PatenteeWhat was I but your journey-man?</span></p>

        <p class="pb-2">
        The animal qualities appropriate to the Ideal or Trusty Servant became variously distributed over time. Hollar appears to depict a wolf's head on his <span class="italic">Picture of a Pattenty</span>despite the <span class="italic">Hogshead</span> of the verseand the derived woodcut illustrating Taylor's <span class="italic">Complaint of M. Tenter-hooke the Projector</span> mentions a <span class="italic">Swines face</span> and <span class="italic">an Asses eares</span>. While I do not doubt that Taylor's verses are in essence an elaboration of those which caption Hollar's engraving, the Water Poet clearly also has the Trusty Servant in mind, for the <span class="italic">Asses eares are to listen unto all I heard/ Wherein your worships profit was prefer'd, and the Swines face is to tast all things, good or bad/ (As Hogs will doe) where profit may be had</span>. 
        </p>

        <p class="pb-2">Much more might be said about the history of this literary-artistic motif,<sup>xiii</sup> and its development from that of the Ideal Man.</p>

