        <h3 class="py-6 text-3xl">The Yale Center 'Panorama'</h3>
        <h4 class="py-2 text-xl">No.29  May/June 2009</h4>
    </div>
    <div class='relative text-left overflow-y-auto'>
      <p class="py-2"> <strong>Copyright Department of Rare Books and Manuscripts at the Yale Center for British Art.</strong> </p>
      <p class="pt-2 pb-4"> <strong>Reproduced by courtesy of the Yale Center for British Art, Paul Mellon Fund. Dimensions of the panorama: H 10 cm x W 8 cm x D 3 cm; dimensions of the central viewing panel: H 8.5 cm x W 7 cm.</strong></p> 
        <span class="float-left pr-6 pb-2"><img :src="thumbnail" width="200"/> <button type="button" @click="getlargerImage()" class="underline font-bold" >View larger image</button></span> 

        <p class="pb-2">This month's subject is an artefact recently acquired by the Yale Center for British Art in New Haven which uses printed images to present a kind of moving panorama of a strikingly modern kind. It comprises a leather-bound wooden box with a screen and two rollers; by rotating the ends of these, it is possible to view frame by frame through the 'screen' of the box a series of images which were evidently cut from a sheet and pasted together into a long continuous strip. This panorama, which has a truly popular appearance, must date - on internal evidence - from around 1680 (see below). If it bears a title or any imprint information, this is inaccessible on account of the sealed nature of the casing, and the same is true of the only other known example of such an artefact, the closely similar <span class="italic">Common Cries of London</span> sheet, now preserved in an exactly comparable case in the Lilly Library of Indiana University at Bloomington</p>

        <p class="pb-2">
        Here we display, first, a view of the box with its rollers and an image appearing within the screen, and second, an attempt to reconstruct the entire sheet from which the strip of images contained within it might have been formed: it seems likely that these might originally have been arranged in three, four, or perhaps five rows as shown here. This reconstruction is intended more for the convenience of displaying all the constituent images than as an assemblage in which I feel much confidence can be placed: however, since to take the object apart would be to destroy it, this is the closest to a reconstruction that we are ever likely to come. 
        </p>

        <p class="pb-2">
        <span class="float-right pr-6 pb-2"><img :src="thumbnail1" width="200"/> <a href="#" @click="getSecondImage()" class="underline font-bold" >View larger image</a></span> 
        </p>

        <p class="pb-2">Apart from the technical interest of the artefact in which it is preserved, the series of images is of considerable importance for the study of late seventeenth-century English popular culture, not least since it includes the earliest depictions of such 'folk heroes', national stereotypes, <span class="italic">singeries</span> and other <span class="italic">drleries</span> as</p>

        <p class="pb-2">
        A Graceless Knave<br>
        An Overdoing Knave<br>
        A flatring Knave<br>
        A Cheating Knave<br>
        Punchenello<br>
        Punchenello's Wife<br>
        Whiping Tom<br>
        Skiping Jone<br>
        A boy with puppies<br>
        Mother Louse<br>
        Time carys away the Pop[e]<br>
        A Baboon playing on a ho boy [oboe]<br>
        A Baboon drinks to a monkey<br>
        A monkey Smokes a pip<br>
        A Baboon plays on a lute<br>
        An Ape barbs<sup>i</sup> the Catt<br>
        A Catt in briches<br>
        A Catt fidleing and myce danceing<br>
        A Bear playing on a harp<br>
        A dog in a Coate<br>
        A hog in armor<br>
        Warping Jack Spanyard<br>
        Monsier Jack with his Stolen hens on his back<br>
        An Apish Mountebanck<br>
        An Apish Costermonger
        </p>

        <p class="pb-2">
        There are thus no fewer than twenty-five individual scenes. The similar <span class="italic">Common Cries of London</span> sheet in the Lilly Library, which has already been noted, features twenty-nine individual criers, but this too unfortunately bears no imprint or other useful dating information, and is rather to be dated from the present sheet than <span class="italic">vice versa</span>. One of the smaller human heads that, together with small animals and birds, powder the background of the present sheet is a crowned male - certainly intended for Charles II - which therefore gives a date-range of 1660-1685 for the original print, but there are various other 'internal' facts that allow us to narrow down the date somewhat, and principally, the presence of 'Whiping Tom'.
        </p>

        <p class="pb-2">
        'Whipping Tom' is first heard of in print in 1681, in two broadside ballads (<span class="italic">Whipping Tom, or, The deceitfull kinsman</span> and <span class="italic">Whipping-Tom turn'd citizen</span>), and a prose sheet entitled <span class="italic">Whipping Tom brought to light, and exposed to view</span>, which at first sight, might seem to narrow the date of our print down to the years 1681-5, except that this latter sheet records that the present mysterious flagellant 'is of the Generation of that Whipping Tom, that about Nine years since proved such an Enemy to the Milk-wenches Bums', thus appearing to attest to the existence of another whipster, known by the same name, and operating c.1672 in the countryside. Other chronological indicators do not afford any greater precision - a half-length portrait of Mother Louse, the celebrated Oxford alewife, was engraved by David Loggan c.1665,<sup>ii</sup> which may have brought her fame to a wider public than the university students for whom she catered, and the present image is certainly dependent on Loggan's, but that does not help us date the Yale sheet. Strictly speaking, then, we may perhaps date it between 1672 and 1685, but I feel happy enough to see it as originating in the earlier half of the 1680s.
        </p>

        <p class="pb-2">
        It is interesting to see the designer copying five engravings made by Hollar back in the early 1640s amongst the sheet's twenty-five figures; the first four - as the captions indicate - are from his <span class="italic">Pake of Knaves</span> with trivial changes, the fifth, 'Time carys away the Pop[e]' was engraved as a single-sheet print by Hollar c.1641.
        </p>

        <p class="pb-2">
        With her right hand she points to the general region of her crotch. Two years after the first edition of the  <span class="italic">Pugillus</span>, the print was reissued in the  <span class="italic">Jeucht Spieghel</span> (1610), and again in the  <span class="italic">Nieuwen Ieucht Spieghe</span>l (Arnhem, 1617). But these seventeenth-century versions are rather less explicit than those of the previous century - at least these women are fully clothed, whereas their predecessors are entirely naked, and drive home the moral rather more pointedly. The  <span class="italic">Pugillus</span> designer may have taken his inspiration from a print engraved by Johannes Wierix issued in 1601, where the words spoken by the naked woman are, if anything, more explicit:
        </p>

        <p class="pb-2">
        I cannot find any evidence for the independent existence of 'Skiping Joan' elsewhere, and it seems probable that she was invented merely as a (rhyming) female partner for Whipping Tom, and similarly provided with a three-thonged whip. 'Punchenello's Wife', on the other hand - known to us nowadays as 'Judy' - and her husband, indeed, are a most interesting presence. Samuel Pepys seems to be the first to have noted a Punch-and-Judy show in England, in his diary entry for 9 May 1662, and references to the figure become more frequent from the later 1660s, though Punch's long-suffering wife does not appear to be mentioned until rather later than the date of our print, and these would appear to be the earliest-known English depictions of either. Punch is far from grotesque here, rather ordinary-looking, in fact, though short and sporting a small moustache; Judy is rather masculine in appearance, despite her full skirt.
        </p>

        <p class="pb-2">
        Two of the figures appear to be foreign nationals, 'Warping Jack Spanyard' and the presumably French 'Monsier Jack with his stolen hens on his back', though neither figure is presented as a caricature visually. I am not clear in quite what sense the Spaniard is 'warping' - nor is it apparent from the image - unless he is meant to be distorting or contorting his body or features. He is, however, clearly derived from the undated <span class="italic">Signiour Bobbadilli Puntado Cavalliro Puff or The Proud Vapouring Spaniard</span> which bears the imprint, 'London Printed Coulered and Sold by Ro:Walton at the Globe and Compasses, in St Paules Church-yard: on the North side', over traces of an earlier one, and is preserved only in the 'tobacciana' collections of two American public libraries.<sup>iii</sup> It may well be that the panorama artist's 'Monsier Jack' similarly derives from another such xenophobic stereotype, perhaps a companion piece, but if so, it is unknown to me. 
        </p>

        <p class="pb-2">
        'A boy with puppies' perhaps derives from one of the two lost prints sold by Stent and his successor Overton, and advertised in their catalogues of 1662 and 1673 as 'Boy with puppies' and 'Boy and puppies',<sup>iv</sup> and for all its diminutive size, this sketch is far from crude; the boy has lifted up his shift (thus exposing his nakedness) and cradles the puppies - four noses [?] can just be made out - in his shirt-lap, while the mother stands on her hind legs in her attempt to reach the pups, her swollen teats evident. There is a mezzotint of the subject published by John Smith in the late 17/early 18 century with traces of an earlier imprint beneath, and that it must have become something of a genre scene success is indicated by a contemporary London delftware figurine found in Lower Marsh, Lambeth, and now in the Museum of London.
        </p>

        <p class="pb-2">
        The remaining twelve images are all of performing animals, and more than half of them <span class="italic">singeries</span> -- in the literal sense of that term. To these we now turn. The taste for pictures of monkeys 'aping' human activities goes back well into the Middle Ages, of course, and the images in the margins of Gothic manuscripts, but the earliest such <span class="italic">prints</span> belong to the final quarter of the fifteenth century, and depict what was already a popular visual motif, the pedlar whose pack is rifled by apes while he sleeps, probably best known today in the version engraved by Pieter van der Heyden after Bruegel in 1562, which, as Janson pointed out, 'served as the <span class="italic">locus classicus</span> of [the] motif from the 1560's until well into the seventeenth century'.<sup>v</sup> He further noted that the widely-travelled Thomas Coryate was puzzled as to the significance of a mural of the motif which he described in some detail as seen on the wall of the inn he stayed at in Lyons in 1608 - interesting incidental evidence of the natural tendency for a seventeenth-century Englishman to assume such a representation must necessarily be 'emblematic' in some way:
        </p>

        <p class="pb-2 italic">
        'on the South side of the wal of another court [of mine Inne], there was a very pretty and merry story painted, which was this: A certaine Pedler having a budget full of small wares, fell asleepe as he was travelling on the way, to whom there came a great multitude of Apes, and robbed him of all his wares while he was asleepe: some of those Apes were painted with pouches or budgets at their backes, which they stole out of the pedlers fardle climing up to trees, some with spectacles on their noses, some with beades about their neckes, some with touch-boxes and inke-hornes in their hands, some with crosses and censour boxes, some with cardes in their hands; al which things they stole out of the budget: and amongst the rest one putting downe the Pedlers breeches, and kissing his naked, &c. This pretty conceit seemeth to import some merry matter, but truely I know not the morall of it.'<sup>vi</sup>  
        </p>

        <p class="pb-2">
        A series of sixteen plates etched c.1560 by Peeter van der Borcht seems to underlie, if not indeed to have originated, the increasing taste for similar <span class="italic"></span>singeries in the later sixteenth and seventeenth centuries. Van der Borcht's <span class="italic"></span>Pedlar Robbed by Apes (on which Bruegel's design depends) is not part of this series, however,<sup>vii</sup> a fact which attests to the pre-existing popularity of the theme, and, indeed, it seems likely that it was the very popularity of the Apes and Pedlar motif which gave Van der Borcht the idea for his series in the first place. His etching is a copy in reverse of van der Heyden's after Bruegel which was first issued by Cock in Antwerp in 1562. Until as recently as 2003 there was no known English print of this very popular European motif, but in that year the British Museum acquired a print which is a slightly enlarged copy in reverse of Van der Borcht's engraving, bearing the imprint, 'Are to be sold by Robert Pricke in White Cross street near Cripplegate Church RP excud.', which was Pricke's address until 1676. The only other known impression, in the Douce collection in Oxford, bears an imperfectly erased imprint which can still be just made out as reading, 'at the Ball in St Pauls Churchyard', which was Pricke's address from c.1677.
        </p>

        <p class="pb-2">
        The designer of the Yale Center panorama was nothing if not eclectic, and all seven of his ape compositions derive from <span class="italic">singeries </span>engraved by Coryn Boel (d.1668) after designs by David Teniers the Younger. Two of them, 'An Ape barbs the Catt' and 'An Apish Mountebanck', derive from his <span class="italic"></span>Barber-surgeon's Shop,<sup>viii</sup> but Boel also engraved a series of six <span class="italic">singeries</span> prints (plus title-page engraving) on which the remaining five ape scenes are based. The 'Apish Costermonger' and 'A Baboon drinks to a monkey'<sup>ix</sup> are based on the print of the two apes playing backgammon; 'A monkey smokes a pipe'- cf. also the print of the cat and pipe-smoking ape playing cards issued in 1646 and surviving uniquely amongst the Thomason Tracts in the British Library  is from that depicting two apes smoking; and 'A Baboon plays on a lute' derives from the print showing two monkeys making music. For his baboon oboist ('A Baboon playing on a ho boy') - especially given the length of the instrument - he probably took the hint from a detail of the title-page engraving. The taste for <span class="italic">singeries</span> was certainly still current c.1690 to judge from the clutch of catalogues of paintings put up for auction around that date, in which we read of 'A piece of Monkeys shaving a Cat',<sup>x</sup> and - if it is not the same item held over as unsold to two further sales - three lots described as 'a Monkey trimming a Cat',<sup>xi</sup> as well as 'Some Monkeys playing at Cards',<sup>xii</sup> 'A Monkeys Feast',<sup>xiii</sup> and 'a p[ie]c[e] of monkeys smoaking and 2 prints'<sup>xiv</sup> - a rare specification of prints for sale at auction, and presumably, prints of <span class="italic">singeries</span>.  Another from Boel's singeries series, two apes eating oysters, is preserved as a print entitled <span class="italic">The Monkeys Mockefeast</span> uniquely in the Pierpont Morgan Library in New York<sup>xv</sup> Though the PML impression has an erased imprint, it is advertised in Stent's 1662 catalogue, and was probably acquired by him in the 1650s, at exactly the time, indeed, when the taste for such <span class="italic">singeries</span> in England appears to begin.
        </p>

        <p class="pb-2">
        I have not been able to point to a similarly precise source for the Yale panorama artist's bear harpist, or his 'Catt fidleing and myce dancing'. Some of these amusing animal images would have been familiar as shop or tavern signs, however, part of the popular art of the street; four Cat & Fiddles, for example, are attested for the 1660s (one a haberdasher's shop, recorded from 1667-77).<sup>xvi</sup> Though we cannot know whether any of these signs included 'myce dancing' while the cat fiddled, this addition would have been common enough, and is found, for example, on an English Delftware charger probably painted in Lambeth c.1730.  A tavern attested in Southwark in the 1650s and 60s had as its sign an ape seated and smoking a pipe.  In similar vein, Chettle refers in passing to a 'signe of the Ape and the Urinall' in <span class="italic">Kind-harts Dreame</span> (1592), as in the present 'Apish Mountebanck' scene. Contemporary with the Yale print, A Hog in Armor is recorded in Fleet Street in 1678,<sup>xix</sup> and was one of the inn-names Addison held up for ridicule in <span class="italic">The Spectator</span> of 2 April 1710:
        </p>

        <p class="pb-2 italic">
        'Our streets are filled with blue boars, black swans, and red lions, not to mention flying pigs   and hogs in armour, with many creatures more extraordinary than any in the deserts of Africa.' <sup>xxi</sup>  
        </p>

        <p class="pb-2">
        But 'a hog in armour' is also a popular phrase, first recorded in English in Howell's <span class="italic">English Proverbs</span> (1659): 'He looketh like a Hogg in armour', and came to mean 'an awkward or clumsy person, stiff and ill at ease in his attire' (OED). Similarly, '[as proud as] a dog in a doublet' was also listed by Howell, though this popular alliterative formula is found as early as 1577, and is perhaps relevant to the Yale sheet's image captioned 'A dog in a Coate'- a sight presumably no less strange than that of 'A Catt in briches'.
        </p>

        <p class="pb-2">
        While prints might also function as toys, as things to amuse the children, of course, there is no guarantee that the Yale Center's 'panorama' of unknown title was <span class="italic">designed</span> for juvenile viewers. The fact that the 'Overdoing Knave' brandishes an alarming clyster-pipe and seems about to administer an enema to the diminutive dog beside him, or that in another Hollar-derived image, 'Time carries off the Pope on his back', does not preclude a youthful audience, however - sensibilities change, <span class="italic">autres temps, autres murs</span>!
        </p>

