        <p class="pb-2">Datable to the time of the First Anglo-Dutch War is a most interesting sheet depicting a type of submarine (really, a semi-submerged ram) built in Rotterdam by the Frenchman De Son in 1653. The English print, <span class="italic">The true and perfect forme of the Strange Ship built at Rotterdam Anno 1653</span>, which though unsigned is attributed to Hollar,<sup>i</sup> clearly copies an original engraved by Frederik de Witt and held uniquely in the Maritime Museum in Rotterdam, entitled <span class="italic">Perfecte Afbeeldinge: van 't Wonderlycke Schip, gemaakt tot Rotterdam</span>. 1653.<sup>ii</sup> Comparison of the texts of the English and Dutch versions is revealing. The original claims that the wonderful ship could reach the East Indies in six weeks, and in a single day get to France and backon the English sheet this latter claim has been significantly altered to read, '[She] can goe from Rotterdam to London and back againe in one day', and is immediately preceded by the ominous 'the inventor doeth undertake in one day to destroy a honderd Ships'. The message was clearhere is a dastardly secret weapon that the Dutch are about to unleash on English shipping! But the English need not have worried, for, in the event, the vessel never saw action, as De Son was unable to find a clockwork motor powerful enough to drive its internal paddle-wheel.  </p>

        <p class="pb-2">The accompanying text is as follows:</p>

        <p class="pb-2">
        'The true & perfect forme of the Strange Shipp built at Rotterdam Anno 1653[;] the inventor of it doeth undertake in one day to destroy one honderd Ships, can goe from Rotterdam to London and back againe in one day: & in 6 Weekes to goe to the East Indiens, and to run as Swift as a bird can fly[:] no fire, nor Storme or Bullets can hinder her unlesse it please God, Although the Ships meane to bee safe in their havens, it is in vaine. for shee shall come to them in any place, it is impossible for her to bee taken, unlesse by treacherie, and then can not bee governed by any but himselfe, the length is 72, the height is 12 foote, the breadth 8 foote, 1. The middle beame, 2. the two ends with Iron barrs, wherein the Strength of the Ship lieth on both ends alike. 3. the rudder of the Ship, 4. the kiele, 5. Iron boults with scrues, 6. the depth to the middle beame, 7. The wheele that goeth round by which it hath its motion, 8. the Shuttles where they goe in, 9. the Gallery there they walke,' 
        </p>

